.poke-resultCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f6d743;
  /* background-color: yellow; */
  border: 1px solid #120136;
  border-radius: 5px;
  margin: 10px;
  padding: 30px;
  flex-grow: 1;
  min-width: 150px;
  font-family: 'Press Start 2P';
}

.pokemon-animated-sprites {
  height: 120px;
  width: 120px;
  padding: 12px;
}
